/* HomePage.css */

/* Center the home page container */
.home-page-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background-color: #ffffff; /* Use a light background color for better readability */
    color: #333;
    border-radius: 8px; /* Add some border radius for a softer look */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Add a subtle box shadow for depth */
    width: 300px; /* Set a fixed width for better form alignment */
  }
  
  .page-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center; /* Center the title text */
  }
  
  .user-greeting {
    font-size: 16px;
    margin-bottom: 20px;
    text-align: center; /* Center the user greeting text */
  }
  
  .form-section {
    margin-bottom: 20px;
  }
  
  .form-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    text-align: center; /* Center the form title */
  }
  
  .form {
    display: flex;
    flex-direction: column;
  }
  
  .form-label {
    margin-bottom: 8px;
  }
  
  .form-input {
    padding: 10px;
    margin-bottom: 10px;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #ccc; /* Add a border to form inputs for better separation */
    border-radius: 4px; /* Add some border radius to form inputs */
  }
  
  .submit-button {
    background-color: #333;
    color: #fff;
    padding: 10px;
    cursor: pointer;
    border: none; /* Remove default button border */
    border-radius: 4px; /* Add some border radius to the button */
    transition: background-color 0.3s; /* Add a smooth transition on button hover */
  }
  
  .submit-button:hover {
    background-color: #555; /* Darken the background on hover */
  }
  